*{margin: 0;padding: 0;}

body{
  font-family:'Karla', serif;
  color:#333333;
}



nav{
  font-family:'Markazi Text', serif;
  font-size: 1.25em;
  display: flex;
  justify-content: space-between;
}

nav ul li{
  list-style: none;
}

nav ul li a{
  text-decoration: none;
}

.menu-icon{
  width: 1em;
  margin: 0.25em;
}

.nav-links-menu{
  position: absolute;
  top: 1.5em;
  background-color: white;
  border: 1px solid gray;
}

.nav-links-menu li{
  margin-left: 0.3em;
  margin-right: 0.5em;
}

.nav-links{
  display: none;
}

#nav-logo{
  height: 1em;
  margin: 0.25em;
}

/* Navigation */

.hero{
  background-color: #495E57;
  color:#EDEFEE;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
}

.hero *{
  /* border:1px solid red; */
}

.hero p{
  margin-left: 0.35em;
  grid-column: 1/1;
  max-width: 300px;
}

h1{
  color:#F4CE14;
  font-size: 1.5em;
  margin-left: 0.25em;
  grid-column: 1/1;
}

.subtitle{
  font-size: 1.125em;
  grid-column: 1/1;
  margin-bottom: 0.5em;
  margin-left: 1em;
}

.btn-primary{
  grid-column: 1/2;
  width: 60%;
  height: 2em;
  border-radius: 5px;
  margin-left:0.25em;
  margin-bottom:0.5em ;
}

.hero-image{
  width:90%;
  border-radius: 16px;
  grid-column: 2/2;
  grid-row: 2/4;
  background-color:#F4CE14;
}